<script>
import { useAccountStore, useFileStore } from '@/store'
import { mapState, mapWritableState } from 'pinia'

import IconClose from '../assets/icon-close.vue'

import { DASHBOARD, UPDATE_CA, UPLOAD_FILE } from '@/store/actions'

export default {
  name: 'EditProfileModal',
  components: {
    IconClose,
  },
  emits: ['close'],
  data() {
    return {
      sending: false,
      newProfilePhoto: null,
    }
  },
  computed: {
    ...mapState(useAccountStore, ['isMaster', 'profilePhotoUrl', 'cid', 'photoHandle']),
    ...mapWritableState(useAccountStore, ['caName']),
    isDisabled() {
      return this.sending
    },
  },
  watch: {},

  mounted() {
    const el = document.getElementById('fileUpload')
    if (el) {
      el.addEventListener('change', (event) => {
        this.newProfilePhoto = event.target.files[0]
      })
    }
  },
  methods: {
    chooseFiles() {
      document.getElementById('fileUpload').click()
    },
    close() {
      this.$emit('close')
    },
    async uploadPhoto(file, folder) {
      const url = await useFileStore()[UPLOAD_FILE]({ file, folder })
        .catch((err) => {
          console.error('Error uploading photo', err)
          reject(err)
        })
      return url
    },
    async updateCA(photoURL) {
      const cid = await useAccountStore()[UPDATE_CA]({ name: this.caName, profilePhotoUrl: photoURL })
        .catch((err) => {
          console.error(err)
          this.sending = false
        })
      if (cid) {
        await useAccountStore()[DASHBOARD]
        this.close()
      }
    },
    async save() {
      this.sending = true
      if (!this.newProfilePhoto) {
        return this.updateCA(this.profilePhotoUrl)
      }

      const newProfilePhotoUrl = await this.uploadPhoto(this.newProfilePhoto)
      await this.updateCA(newProfilePhotoUrl)
    },
  },

}
</script>

<template>
  <transition name="modal-fade">
    <div v-show="true" class="modal-backdrop">
      <div
        class="modal-wallid edit-profile-modal"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
        @click.stop
      >
        <header id="modalTitle" class="modal-header">
          <slot name="header">
            {{ $t('editProfile.title') }}
            <v-spacer />
            <button
              type="button"
              class="btn-close"
              aria-label="Close modal"
              @click="close"
            >
              <IconClose />
            </button>
          </slot>
        </header>

        <div class="modal-body text-center pb-9">
          <slot name="body">
            <v-container id="scroll" class="pb-16 scroll">
              <v-row>
                <v-col
                  id="photo-container"
                  style="justify-content: center"
                  cols="4"
                  md="3"
                  class="pb-2"
                >
                  <div class="wrapper">
                    <div
                      class="company-header-avatar"
                      :style="
                        `background-image: url(${profilePhotoUrl})`
                      "
                    />
                    <ImageProfile v-if="!(profilePhotoUrl)" />
                  </div>
                </v-col>
                <v-col
                  id="errorUrlPhoto"
                  cols="8"
                  md="8"
                  class="input-field pb-2 text-left"
                >
                  <p class="small_text">
                    {{ $t('createCertModal.upload[0]') }}
                  </p>
                  <p class="mb-0 small_text">
                    {{ $t('createCertModal.upload[1]') }}
                  </p>
                  <p class="small_text">
                    {{ $t('createCertModal.upload[2]') }}
                  </p>
                  <input
                    id="fileUpload"
                    type="file"
                    hidden
                    accept="image/x-png,image/gif,image/jpeg"
                  />
                  <v-btn class="back" @click="chooseFiles()">
                    {{ $t('createCertModal.uploadButton') }}
                  </v-btn>
                </v-col>
                <v-col cols="12" md="12" class="input-field pb-2 text-left">
                  <label class="">{{ $t('createCAModal.caName') }}</label>
                  <v-text-field
                    v-model="caName"
                    class=""
                    flat
                    solo
                    persistent-hint
                    :hint="$t('createCAModal.hint')"
                  />
                </v-col>
              </v-row>
            </v-container>
          </slot>
        </div>

        <div class="modal-footer">
          <slot name="footer">
            <v-row class="justify-end" no-gutters>
              <v-btn
                class="next"
                :loading="isDisabled"
                :disabled="isDisabled"
                @click="save()"
              >
                {{ $t('editProfile.save') }}
              </v-btn>
            </v-row>
          </slot>
        </div>
      </div>
    </div>
  </transition>
</template>

